* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html {
	background-color: $background-color;
	font-size: 16px;
	font-family: 'Montserrat', sans-serif;
	letter-spacing: 1px;
	color: $font-color;
	@include mq(768px) {
		font-size: 22px;
	}
}

body {
	width: 100%;
	min-height: 100vh;
	@include flexCenter;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	padding: 0.5rem;
}

main {
	width: 100%;
	max-width: 800px;
	max-height: 1050px;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	color: $font-color;
	background-color: $mask-color;
	border: 1px solid $bordercolor;
	border-radius: 10px;
	box-shadow: 1px 1px 2.5px $shadowcolor;
}

img {
	display: block;
}
.main__h1 {
	font-family: 'Oleo Script Swash Caps', cursive;
	color: $h1-color;
}

hr {
	width: 90%;
	margin-top: 10px;
	margin-inline: auto;
	border: 1px solid $bordercolor;
}
ul,
ol {
	list-style: none;
}
